.nav-inner {
  position: relative;
  display: block;
  background-color: var(--back-basic);
  box-shadow: 0 20px 30px 0px rgba(0, 0, 0, 0.7);
  z-index: 5;
}

.nav-inner__wrap {
  position: relative;
  display: grid;
  grid-template-columns: 96px 1fr;
  column-gap: 64px;
  padding-top: 16px;
  padding-bottom: 60px;
}
