.site-list {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 70px;
  row-gap: 50px;
  padding-top: 30px;
  padding-left: 20px;
}

.site-list a {
  position: relative;
  line-height: 47px;
  color: var(--basic-white);
  text-decoration: none;
}

.site-list a::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 47px;
  top: 50%;
  left: -20px;
  transform: translateY(-50%);
  background-color: #33cc33;
  border-right: 1px solid #000000;
}



