@charset "UTF-8";
@font-face {
  font-family: "GothamPro";
  src: url("../fonts/GothaProNarMed.woff2") format("woff2"), url("../fonts/GothaProNarMed.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: "GothamPro";
  src: url("../fonts/GothaProReg.woff2") format("woff2"), url("../fonts/GothaProReg.woff") format("woff");
  font-weight: 100;
  font-style: normal; }

@font-face {
  font-family: "GothamPro";
  src: url("../fonts/GothaProNarBol.woff2") format("woff2"), url("../fonts/GothaProNarBol.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

body {
  font-family: "GothamPro", "Arial", sans-serif;
  font-weight: 100;
  margin: 0;
  padding: 0; }

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  margin: 0; }

ul {
  padding: 0;
  margin: 0;
  list-style-type: none; }

h1,
h2 {
  text-transform: uppercase;
  margin: 0;
  word-break: normal; }

input[name="toggle"] {
  display: none; }

/*  Colors  */
:root {
  --back-basic: #23413f;
  --basic-white: #ffffff;
  --main-text: #23413f;
  --basic-label: #006634;
  --basic-link: #38a937;
  --button: #214d53; }

/*  Text  */
:root {
  --title-family: "GothamPro", "Arial", sans-serif;
  --title-size: 75px;
  --main-text-size: 25px; }

.advantages {
  background: linear-gradient(to top, #022e5f, #01487f, #014d85); }

.advantages__wrap {
  padding-top: 48px;
  padding-bottom: 48px; }

.advantages__list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  column-gap: 125px;
  row-gap: 40px;
  text-align: center;
  margin-top: 56px; }

.advantages img {
  margin-bottom: 25px; }

.advantages h2,
.advantages p {
  color: var(--basic-white);
  margin: 0; }

.advantages p {
  font-size: 26px; }

.application-form {
  padding-top: 65px;
  padding-bottom: 48px; }

.application-form h2 {
  margin: 0;
  margin-bottom: 42px; }

.application-form p {
  padding-right: 200px;
  margin: 0; }

.application-form a {
  font-size: 26px;
  letter-spacing: -.6px; }

.application-form__user {
  display: grid;
  grid-template-columns: 800px 778px;
  column-gap: 20px;
  margin-top: 42px;
  margin-bottom: 50px; }

.application-form__btn {
  display: block;
  width: 270px;
  margin-left: auto;
  margin-top: 5px; }

.application-form__user p {
  display: inline-block;
  height: 54px;
  padding-right: 0; }

.application-form__user label {
  font-family: inherit;
  font-size: 32px;
  line-height: 24px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--basic-label);
  margin-right: 15px;
  vertical-align: bottom; }

.application-form__user input {
  width: 600px;
  height: 100%;
  font-family: inherit;
  font-size: 32px;
  font-weight: 100;
  letter-spacing: -0.6px;
  color: #9c9c9c;
  border: none;
  border-bottom: 2px solid var(--back-basic);
  padding-left: 24px;
  padding-right: 24px; }

.btn {
  font-size: 25px;
  line-height: 20px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--basic-white);
  background-color: var(--button);
  text-align: center;
  padding: 20px;
  border: none;
  cursor: pointer; }

.business {
  background-color: var(--back-basic); }

.business__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 90px;
  padding-top: 54px;
  padding-bottom: 57px; }

.business__list {
  padding-top: 54px;
  padding-bottom: 57px; }

.business__list p,
.business__list a {
  color: var(--basic-white);
  margin: 0; }

.business__list p {
  margin-bottom: 40px; }

.business__title,
.business__tel {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase; }

p.business__title {
  margin-bottom: 47px; }

.business__tel {
  display: block;
  line-height: 42px;
  text-decoration: none; }

.business__info a {
  font-weight: 100; }

.clean-rooms {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  padding-bottom: 45px; }

.clean-rooms__slider {
  width: 100%;
  min-height: 430px;
  display: grid;
  grid-template-columns: repeat(6, 283px);
  column-gap: 130px;
  margin-top: 43px;
  overflow: hidden; }

.clean-rooms__slider img {
  margin-bottom: 35px; }

.clean-rooms h4 {
  font-size: 23px;
  line-height: 25px;
  font-weight: 300;
  letter-spacing: -1px;
  margin: 0;
  margin-bottom: 25px; }

.clean-rooms p {
  margin: 0; }

.clean-rooms__toggle {
  position: absolute;
  width: 60px;
  height: 26px;
  top: 230px;
  background-repeat: no-repeat;
  cursor: pointer; }

.clean-rooms__toggle--prev {
  left: 54px;
  background-image: url("../img/arrow-clean-rooms.png");
  opacity: 0.5; }

.clean-rooms__toggle--next {
  right: 54px;
  background-image: url("../img/arrow-clean-rooms.png");
  transform: scaleX(-1); }

.container {
  max-width: 1920px;
  padding-left: 160px;
  padding-right: 160px;
  margin: 0 auto; }

.form-search {
  display: flex;
  background-image: url("../img/search.png");
  background-repeat: no-repeat;
  width: 35px;
  height: 35px;
  cursor: pointer;
  margin-left: auto;
  outline: 1px solid red; }

.form-search input {
  display: none; }

.form-search button {
  visibility: hidden; }

.innovation {
  padding-top: 64px;
  padding-bottom: 60px; }

.innovation h2 {
  display: grid;
  grid-template-columns: 1fr;
  padding-bottom: 47px; }

.innovation__list {
  display: grid;
  grid-template-columns: 412px 708px 430px;
  column-gap: 23px; }

.innovation__list li {
  position: relative;
  min-height: 693px; }

.innovation__list img {
  width: auto;
  height: 378px; }

.innovation__btn {
  display: block;
  width: 278px;
  text-decoration: none;
  margin-top: 47px; }

.innovation__list li:nth-child(2) .innovation__desc {
  position: absolute;
  width: 450px;
  left: 50%;
  transform: translateX(-50%); }

.innovation__desc {
  margin-top: 18px; }

.innovation__desc h3 {
  font-size: 30px;
  line-height: 26px;
  font-weight: 300;
  margin: 0;
  margin-bottom: 25px; }

.innovation__desc p {
  margin: 0; }

.innovation__list li:nth-child(1) p {
  padding-right: 80px; }

.innovation__list li:nth-child(3) p {
  padding-right: 120px; }

.innovation__item-current::before {
  display: block;
  position: absolute;
  content: "";
  width: 640px;
  height: 675px;
  top: 19px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/back-hover.svg");
  background-repeat: no-repeat;
  z-index: -1; }

.logo-tdi {
  width: 96px;
  height: 99px; }

.main-nav {
  display: flex;
  flex-wrap: wrap;
  padding-top: 57px;
  padding-bottom: 59px;
  padding-left: 254px; }

.main-nav li {
  margin-right: 46px; }

.main-nav li:last-child {
  margin-right: 0; }

.main-nav a {
  color: var(--main-text);
  text-decoration: none; }

.navigation-out {
  position: relative;
  padding-top: 20px;
  padding-bottom: 23px;
  box-shadow: 0 20px 35px 20px rgba(0, 0, 0, 0.7);
  z-index: 10; }

.navigation-out__wrap {
  display: flex;
  align-items: baseline; }

.navigation-out__toggle {
  width: 40px;
  height: 15px;
  margin-right: 50px; }

.navigation-out__tel {
  display: inline-block;
  font-family: inherit;
  font-size: 32px;
  font-weight: 700;
  color: var(--main-text);
  text-decoration: none;
  padding-bottom: 20px;
  margin-right: 40px; }

.navigation-out__toggle {
  position: relative;
  display: inline-block;
  margin-right: 50px;
  cursor: pointer; }

.navigation-out__toggle-inner--1,
.navigation-out__toggle-inner--2,
.navigation-out__toggle-inner--3 {
  width: 40px;
  height: 3px;
  background-color: var(--back-basic);
  margin-bottom: 3px;
  transition: 0.4s; }

/* Поворот первой полосы */
.change .navigation-out__toggle-inner--1 {
  -webkit-transform: rotate(-45deg) translate(-6px, 3px);
  transform: rotate(-45deg) translate(-6px, 3px); }

/* Исчезание второй полосы */
.change .navigation-out__toggle-inner--2 {
  opacity: 0; }

/* Поворот последней полосы */
.change .navigation-out__toggle-inner--3 {
  -webkit-transform: rotate(45deg) translate(-5px, -3px);
  transform: rotate(45deg) translate(-5px, -3px); }

.nav-inner {
  position: relative;
  display: block;
  background-color: var(--back-basic);
  box-shadow: 0 20px 30px 0px rgba(0, 0, 0, 0.7);
  z-index: 5; }

.nav-inner__wrap {
  position: relative;
  display: grid;
  grid-template-columns: 96px 1fr;
  column-gap: 64px;
  padding-top: 16px;
  padding-bottom: 60px; }

.page-footer__right {
  text-align: right;
  padding-top: 54px;
  padding-bottom: 70px; }

.site-list {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 70px;
  row-gap: 50px;
  padding-top: 30px;
  padding-left: 20px; }

.site-list a {
  position: relative;
  line-height: 47px;
  color: var(--basic-white);
  text-decoration: none; }

.site-list a::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 47px;
  top: 50%;
  left: -20px;
  transform: translateY(-50%);
  background-color: #33cc33;
  border-right: 1px solid #000000; }

/*
.site-list__more a::after {
  position: absolute;
  content: "";
  width: 17px;
  height: 9px;
  right: -55px;
  background: transparent;
  background-image: url("../img/arrow-menu.svg");
  background-repeat: no-repeat;
  border-right: none;
}
*/
.site-list-inner {
  position: absolute;
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 60px;
  padding-bottom: 40px;
  top: 120px;
  left: 320px;
  padding-top: 50px;
  background-color: var(--back-basic);
  z-index: 5; }

.site-list-inner a {
  line-height: 47px; }

.site-list-inner a::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 100%;
  right: -29px;
  background-color: #33cc33; }

.slider-main {
  position: relative;
  width: 100%; }

.slider-main__list {
  width: 100%;
  height: 621px;
  overflow: hidden; }

.slider-main__item {
  display: flex;
  max-width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover; }

.slider-main__item--1 {
  background-image: url("../img/earth.jpg"); }

.slider-main__item--2 {
  background-image: url("../img/urban.jpg"); }

.slider-main__item--3 {
  background-image: url("../img/sundown.jpg"); }

.slider-main__desc {
  margin-top: 104px; }

.slider-main__link {
  width: 100%;
  height: 621px; }

.slider-main h2 {
  font-size: 60px;
  color: var(--basic-white);
  margin-bottom: 32px; }

.slider-main p {
  font-size: 34px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--basic-white);
  margin: 0; }

.slider-main a {
  text-decoration: none; }

.slider-main__toggle {
  position: absolute;
  width: 60px;
  height: 26px;
  top: 275px;
  background-repeat: no-repeat;
  cursor: pointer; }

.slider-main__toggle--prev {
  left: 54px;
  background-image: url("../img/arrow-slider-main.png");
  opacity: 0.5; }

.slider-main__toggle--next {
  right: 54px;
  background-image: url("../img/arrow-slider-main.png");
  transform: scaleX(-1); }

.headline {
  font-family: inherit;
  font-size: 56px;
  line-height: 55px;
  font-weight: 700;
  color: var(--main-text);
  text-transform: uppercase; }

.text-site {
  font-family: inherit;
  font-size: 23px;
  line-height: 26px;
  font-weight: 100;
  letter-spacing: -0.6px;
  color: var(--main-text); }

.menu-text {
  font-family: inherit;
  font-size: 23px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--main-text); }

.site-link {
  font-family: inherit;
  font-size: 20px;
  line-height: 24px;
  font-weight: 100;
  color: var(--basic-link); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
  clip-path: inset(100%); }
