@mixin _1600 {
    @media (max-width: 1600px){
        @content;
    }
}

@mixin _1280 {
    @media (max-width: 1280px){
        @content;
    }
}

@mixin _960 {
    @media (max-width: 960px){
        @content;
    }
}

@mixin _767 {
    @media (max-width: 767px){
        @content;
    }
}

@mixin _480 {
    @media (max-width: 480px){
        @content;
    }
}





