.application-form {
  padding-top: 65px;
  padding-bottom: 48px;
}

.application-form h2 {
  margin: 0;
  margin-bottom: 42px;
}

.application-form p {
  padding-right: 200px;
  margin: 0;
}

.application-form a {
  font-size: 26px;
  letter-spacing: -.6px;
}

.application-form__user {
  display: grid;
  grid-template-columns: 800px 778px;
  column-gap: 20px;
  margin-top: 42px;
  margin-bottom: 50px;
}

.application-form__btn {
  display: block;
  width: 270px;
  margin-left: auto;
  margin-top: 5px;
}

.application-form__user p {
  display: inline-block;
  height: 54px;
  padding-right: 0;
}

.application-form__user label {
  font-family: inherit;
  font-size: 32px;
  line-height: 24px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--basic-label);
  margin-right: 15px;

  vertical-align: bottom;
}

.application-form__user input {
  width: 600px;
  height: 100%;
  font-family: inherit;
  font-size: 32px;
  font-weight: 100;
  letter-spacing: -0.6px;
  color: #9c9c9c;
  border: none;
  border-bottom: 2px solid var(--back-basic);
  padding-left: 24px;
  padding-right: 24px;
}

