.slider-main {
  position: relative;
  width: 100%;
}

.slider-main__list {
  width: 100%;
  height: 621px;
  overflow: hidden;
}

.slider-main__item {
  display: flex;
  max-width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.slider-main__item--1  {
  background-image: url("../img/earth.jpg");
}

.slider-main__item--2  {
  background-image: url("../img/urban.jpg");
}

.slider-main__item--3  {
  background-image: url("../img/sundown.jpg");
}

.slider-main img {

}

.slider-main__desc {
  margin-top: 104px;
}

.slider-main__link {
  width: 100%;
  height: 621px;
}

.slider-main h2 {
  font-size: 60px;
  color: var(--basic-white);
  margin-bottom: 32px;
}

.slider-main p {
  font-size: 34px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--basic-white);
  margin: 0;
}

.slider-main a {
  text-decoration: none;
}

.slider-main__toggle {
  position: absolute;
  width: 60px;
  height: 26px;
  top: 275px;
  background-repeat: no-repeat;
  cursor: pointer;
}

.slider-main__toggle--prev {
  left: 54px;
  background-image: url("../img/arrow-slider-main.png");
  opacity: 0.5;
}

.slider-main__toggle--next {
  right: 54px;
  background-image: url("../img/arrow-slider-main.png");
  transform: scaleX(-1);
}




