.advantages {
  background: linear-gradient(to top, #022e5f, #01487f, #014d85);
}

.advantages__wrap {
  padding-top: 48px;
  padding-bottom: 48px;
}

.advantages__list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  column-gap: 125px;
  row-gap: 40px;
  text-align: center;
  margin-top: 56px;
}

.advantages img {
  margin-bottom: 25px;
}

.advantages h2,
.advantages p {
  color: var(--basic-white);
  margin: 0;
}

.advantages p {
  font-size: 26px;
}

