.innovation {
  padding-top: 64px;
  padding-bottom: 60px;
}

.innovation h2 {
  display: grid;
  grid-template-columns: 1fr;
  padding-bottom: 47px;
}

.innovation__list {
  display: grid;
  grid-template-columns: 412px 708px 430px;
  column-gap: 23px;
}

.innovation__list li {
  position: relative;
  min-height: 693px;
}

.innovation__list img {
  width: auto;
  height: 378px;
}

.innovation__btn {
  display: block;
  width: 278px;
  text-decoration: none;
  margin-top: 47px;
}

.innovation__list li:nth-child(2) .innovation__desc {
  position: absolute;
  width: 450px;
  left: 50%;
  transform: translateX(-50%);
}

.innovation__desc {
  margin-top: 18px;
}

.innovation__desc h3 {
  font-size: 30px;
  line-height: 26px;
  font-weight: 300;
  margin: 0;
  margin-bottom: 25px;
}

.innovation__desc p {
  margin: 0;
}

.innovation__list li:nth-child(1) p {
  padding-right: 80px;
}

.innovation__list li:nth-child(3) p {
  padding-right: 120px;
}

.innovation__item-current::before {
  display: block;
  position: absolute;
  content: "";
  width: 640px;
  height: 675px;
  top: 19px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/back-hover.svg");
  background-repeat: no-repeat;
  z-index: -1;
}

