.clean-rooms {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  padding-bottom: 45px;
}

.clean-rooms__slider {
  width: 100%;
  min-height: 430px;
  display: grid;
  grid-template-columns: repeat(6, 283px);
  column-gap: 130px;
  margin-top: 43px;
  overflow: hidden;
}

.clean-rooms__slider article {

}

.clean-rooms__slider img {
  margin-bottom: 35px;
}

.clean-rooms h4 {
  font-size: 23px;
  line-height: 25px;
  font-weight: 300;
  letter-spacing: -1px;
  margin: 0;
  margin-bottom: 25px;
}

.clean-rooms p {
  margin: 0;
}

.clean-rooms__toggle {
  position: absolute;
  width: 60px;
  height: 26px;
  top: 230px;
  background-repeat: no-repeat;
  cursor: pointer;
}

.clean-rooms__toggle--prev {
  left: 54px;
  background-image: url("../img/arrow-clean-rooms.png");
  opacity: 0.5;
}

.clean-rooms__toggle--next {
  right: 54px;
  background-image: url("../img/arrow-clean-rooms.png");
  transform: scaleX(-1);
}
