.business {
  background-color: var(--back-basic);
}

.business__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 90px;
  padding-top: 54px;
  padding-bottom: 57px;
}

.business__list {
  padding-top: 54px;
  padding-bottom: 57px;
}

.business__list p,
.business__list a {
  color: var(--basic-white);
  margin: 0;
}

.business__list p {
  margin-bottom: 40px;
}

.business__title,
.business__tel {
  font-size: 30px;
  font-weight: 300;
  text-transform: uppercase;
}

p.business__title {
  margin-bottom: 47px;
}

.business__tel {
  display: block;
  line-height: 42px;
  text-decoration: none;
}

.business__info a {
  font-weight: 100;
}
